#header{
    width: 100%;
    height: 80px;
border: 2px solid black;
text-align: center;
align-items: center;  margin-bottom: 40px;
align-content: center;}
#add{
    width: 40%;
    height: 50%;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    
}
#list{border: 2px solid black;
    margin-bottom: 40px;
    margin-top: 90px;

}
       
#expense{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border: 2px solid black;
    margin: 10px;
    border-radius: 20px;
    background-color: aquamarine;
    justify-content: space-between;
    color: black;
    box-sizing: content-box;
height: 40px;
align-items: center;
justify-content:space-between;

}
#expense button{
    border-radius: 20px;
}
#expense input{
    color: black;
    font-size: larger;
}
#chart{
    width: 40%;
    height: 50%;
   
    display: flex;
    flex-direction: column;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 10%;
}